// btn2--return ()
.btn2.btn2--return {
  background: #a4adbc;
}

.btn2.btn2--return:hover {  // hover
  opacity: 0.6;
}

.btn2.btn2--return:hover .btn2__arrow {  // hover
  transform: translateX(rem(-5));
}

.btn2.btn2--return .btn2__arrow {
  position: relative;
}

.btn2.btn2--return .btn2__arrow::before {  // 
  display: inline-block;
  content: "";
  width: rem(85);
  height: rem(10.86);
  background: url(../images/common/btn2-arrow-white-return.webp) no-repeat center center / contain;
  transition: transform 0.2s ease-out;

  @include mq(md) {
    right: rem(35);
  }
}



