@use "global" as *;


.images03 {

}

.images03__contents {
  display: grid;
  grid-auto-flow: dense;  /*  */
  grid-template-columns: 585fr 585fr;
  grid-auto-rows: 369fr 369fr;

  column-gap: rem(29);
  row-gap: rem(48);
  align-items: flex-start;  // 

  @include mq(md) {
    grid-template-columns: 157fr 157fr;
    grid-auto-rows: 350fr 105fr;
    column-gap: rem(10);
    row-gap: rem(10);
  }
}

.images03__contents-item {
  max-width: rem(585);

  @include mq(md) {
    max-width: auto;
  }
}

.images03__contents-item:nth-child(1) {
  grid-column: 1; /* 1 */
  grid-row: 1 / 3; /* 12 */

  @include mq(md) {
    grid-column: 1 / 3; /* 12 */
    grid-row: 1; /* 1 */
    margin-inline: auto;
  }
}

.images03__contents-item:nth-child(2) {
  grid-column: 2; /* 2 */
  grid-row: 1; /* 1 */

  @include mq(md) {
    grid-column: 1; /* 1 */
    grid-row: 2; /* 2 */
  }
}

.images03__contents-item:nth-child(3) {
  grid-column: 2; /* 2 */
  grid-row: 2; /* 2 */

  @include mq(md) {
    grid-column: 2; /* 2 */
    grid-row: 2; /* 2 */
  }
}
