@use "global" as *;


.drawer-menu {
  display: none;

  @include mq(md) {
    display: block;
    border-top: rem(4) solid #488ad9;
    overflow: scroll;  // 
    background:  $color-white;
    width: 70%;

    position: fixed;
    top: rem(77);  // header
    right: 0;
    z-index: 400;
    transform: translateY(-200%);  // 
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;

    /*  */
    // height: calc(100vh - rem( 77)); /*  -  */
    max-height: calc(100vh - rem(77)); /* :  -  */
    height: auto; /*  */
  }
}

.drawer-menu.is-clicked {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

.drawer-menu__wrap {
  @include mq(md) {
    /*  */
    height: 100%; /*  */
    overflow-y: auto; /*  */
  }
}

.drawer-menu__nav {
  @include mq(md) {

  }
}



/*  */
.js-drawer-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; /*  */
  height: 100%; /*  */
  background-color: rgba(163, 169, 181, 0.5); /* :  */
  z-index: 399; /*  */
  display: none; /*  */
}

/*  */
.js-drawer-overlay.is-active {
  display: block;
}

