@use "global" as *;


.list-item {
  font-size: rem(17);
  font-weight: $normal;
  color: #1e1e1e;
  text-align: left;
  line-height: 1.75;
  padding-left: rem(25);  // 
  position: relative;

  @include mq(md) {
    font-size: rem(14);
    line-height: 1.7;
    padding-left: rem(20);
  }
}

.list-item::before {  // 
  content: '';
  color: #569adf;
  position: absolute;
  top: rem(0);
  left: 0;
}




// =======================================
// 
// =======================================
// list-item--2 ()
.list-item.list-item--2 {
  line-height: 2.0;

  @include mq(md) {
    font-size: rem(13);
    padding-left: rem(18);
  }
}

.list-item.list-item--2::before {  // 
  font-size: rem(14);
  top: rem(3);

  @include mq(md) {
    font-size: rem(10);
    top: rem(2);
  }
}


// list-item--red () 
.list-item.list-item--red {
  color: #364c6a;
  padding-left: rem(33);

  @include mq(md) {
    padding-left: rem(18);
  }
}

.list-item.list-item--red::before {  // 
  color: #ef7b99;
  font-size: rem(14);
  top: rem(3);

  @include mq(md) {
    font-size: rem(10);
    top: rem(2);
  }
}


// list-item--red-2 () 
.list-item.list-item--red-2 {
  padding-left: rem(25);
}

.list-item.list-item--red-2::before {
  color: #ef7b99;
  font-size: rem(14);
  top: rem(3);

  @include mq(md) {
    font-size: rem(10);
    top: rem(2);
  }
}
