@use "sass:math";

////////////////////
// 
////////////////////

// ()
$inner: 1200px;

// 
$padding-pc: 40px;
$padding-sp: 25px;

////////////////////
// 
////////////////////

// font-family ()
$font-family-base: "Noto Sans JP", sans-serif;
$font-family-en-1: "Montserrat", serif;
$font-family-en-2: "Murecho", serif;

// font-size (body)
$font-size-base: rem(16);

// font-weight (body)
$normal: 400;
$medium: 500;
$semiBold: 600;
$bold: 700;

// line-height (body) (line-heightreset.scss(1.75))
// $line-height-base: 1.9;


////////////////////
// 
////////////////////

//
$color-font-base-1: #1e1e1e;
$color-font-base-2: #333333;
// $color-font-base-3: #190300;
$color-background-base-1: #f4f6fa;
// $color-background-base-2: #998676;
$color-white: #fff;
$color-accent1: #1d61af;  // headerfooter
$color-accent2: #569adf;  // header
$color-accent3: #2c81d8;  // headerhover

// ====================================
body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  font-weight: $normal;
  color: $color-font-base-1;
  font-style: normal;
}

/*  */
#wpadminbar {
  display: none !important;
}
