@use "global" as *;


.input {

}

.input__text {  // 1
  border: rem(1) solid #c2cbdb;
  background: #FFF;

  padding-block: rem(9);
  padding-inline: rem(21);

  // 
  color: $color-font-base-2;
  font-size: rem(17);
  font-weight: $normal;
  line-height: normal;

  transition: background 0.2s ease-out;

  @include mq(md) {
    padding-block: rem(7);
    padding-inline: rem(6);
    font-size: rem(14);
  }
}

.input__text:hover,
.input__text:focus {  //focus
  // background: rgba(64, 224, 208, 0.1); /*  + 30 */
}

.input__text::placeholder {  // CSS (placeholder)
  color: #c4c4c4;
  font-size: rem(17);
  font-weight: $normal;
  line-height: normal;

  @include mq(md) {
    font-size: rem(14);
  }
}

