@use "global" as *;


.contents03 {

}

.contents03__img {
  aspect-ratio: 1200 / 800;
  max-width: rem(1200);
  width: 100%;
  object-fit: cover;
}

.contents03 .contents03__img img {  // .contents03
  aspect-ratio: 1200 / 800;         // the_content();(img).works-contents__content img
  max-width: rem(1200);
  width: 100%;
  object-fit: cover;
  margin-top: 0;  // .works-contents__content img
}

.contents03 .contents03__text {  // .contents03
  margin-top: rem(10);
  font-size: rem(16);
  font-weight: $medium;
  color: #000;
  margin-top: 0;  // .works-contents__content p

  @include mq(md) {
    margin-top: rem(5);
    font-size: rem(12);
  }
}


// 1()
.contents03:nth-of-type(1) .contents03__img img {  // .contents03
  aspect-ratio: 585 / 863;         // the_content();(img).works-contents__content img
  max-width: rem(585);
  width: 100%;
  object-fit: cover;
  margin-top: 0;  // .works-contents__content img
}

.contents03:nth-of-type(1) .contents03__img {
  aspect-ratio: 585 / 863;
  max-width: rem(585);
