@use "global" as *;


// WP-PageNavi
.wp-pagenavi {
}

.wp-pagenavi__prev {
}

.wp-pagenavi__current {
}

.wp-pagenavi__number {
}

.wp-pagenavi__next {
}



/*  */
.wp-pagenavi {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(12);

  @include mq(md) {
    gap: rem(8);
  }
}

/* ======  ====== */
.wp-pagenavi .page {
  height: rem(30);  // OK
  min-width: rem(30);  // 
  padding-inline: rem(3);  // padding
  display: grid;
  place-items: center;

  color: $color-font-base-1;
  font-size: rem(16);
  line-height: normal;
  transition: background 0.2s ease-out;

  @include mq(md) {
    height: rem(28);  // OK
    min-width: rem(28);  // 
    font-size: rem(13);
  }
}

.wp-pagenavi .page:hover {  // hover
  background: #eef0f1;
  border-radius: rem(5);
}

/* ======  () ====== */
.wp-pagenavi .current {
  height: rem(30);  // OK
  min-width: rem(30);  // 
  padding-inline: rem(3);  // padding
  display: grid;
  place-items: center;

  color: $color-font-base-1;
  font-size: rem(16);
  line-height: normal;
  background: #eef0f1;
  border-radius: rem(5);

  @include mq(md) {
    height: rem(28);  // OK
    min-width: rem(28);  // 
    font-size: rem(13);
  }
}


/* ======  ====== */
.wp-pagenavi .pages {  // 1/12pages
  display: none;
}


/* ======  ====== */
.wp-pagenavi .first {
  margin-right: rem(18);
  font-size: rem(24);
  display: grid;
  place-items: center;
  position: relative;
  width: rem(16);
  height: rem(16);
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    margin-right: rem(10);
    font-size: rem(13);
    width: rem(14);
    height: rem(14);
  }
}

.wp-pagenavi .first:hover {
  opacity: 0.4;
}

.wp-pagenavi .first::before,
.wp-pagenavi .first::after {
  content: "";
  width: rem(16); /*  */
  height: rem(16); /*  */
  border-top: 1.5px solid $color-font-base-2;
	border-left: 1.5px solid $color-font-base-2;

  position: absolute;
  top: 50%;
  left: 0;

  @include mq(md) {
    width: rem(14);
    height: rem(14);
  }
}

.wp-pagenavi .first::before {  // 
  transform: translate(0,-50%) rotate(-45deg);
}

.wp-pagenavi .first::after {  // 
  transform: translate(-5px,-50%) rotate(-45deg);
}


/* ======  ====== */
.wp-pagenavi .last {
  margin-left: rem(18);
  font-size: rem(24);
  display: grid;
  place-items: center;
  position: relative;
  width: rem(16);
  height: rem(16);
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    margin-left: rem(10);
    font-size: rem(13);
    width: rem(14);
    height: rem(14);
  }
}

.wp-pagenavi .last:hover {
  opacity: 0.4;
}

.wp-pagenavi .last::before,
.wp-pagenavi .last::after {
  content: "";
  width: rem(16); /*  */
  height: rem(16); /*  */
  border-top: 1.5px solid $color-font-base-2;
	border-right: 1.5px solid $color-font-base-2;

  position: absolute;
  top: 50%;
  left: 0;

  @include mq(md) {
    width: rem(14);
    height: rem(14);
  }
}

.wp-pagenavi .last::before {  // 
  transform: translate(0,-50%) rotate(45deg);
}

.wp-pagenavi .last::after {  // 
  transform: translate(-5px,-50%) rotate(45deg);
}


/* ======  ====== */
.wp-pagenavi .previouspostslink {
  margin-right: rem(30);

  @include mq(md) {
    margin-right: rem(10);
  }
}

.wp-pagenavi .nextpostslink {
  margin-left: rem(30);

  @include mq(md) {
    margin-left: rem(10);
  }
}
.wp-pagenavi .previouspostslink,
.wp-pagenavi .nextpostslink {
  font-size: rem(24);
  display: grid;
  place-items: center;
  position: relative;
  width: rem(16);
  height: rem(16);
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    font-size: rem(13);
    width: rem(14);
    height: rem(14);
  }
}

.wp-pagenavi .previouspostslink:hover,
.wp-pagenavi .nextpostslink:hover {  // hover
  opacity: 0.4;
}

.wp-pagenavi .previouspostslink::before {  //
  content: "";
  width: rem(16); /*  */
  height: rem(16); /*  */
  border-top: 1.5px solid $color-font-base-2;
	border-left: 1.5px solid $color-font-base-2;

  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0,-50%) rotate(-45deg);

  @include mq(md) {
    width: rem(14);
    height: rem(14);
  }
}

.wp-pagenavi .nextpostslink::after {  //
  content: "";
  width: rem(16); /*  */
  height: rem(16); /*  */
  border-top: 1.5px solid $color-font-base-2;
	border-right: 1.5px solid $color-font-base-2;

  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0,-50%) rotate(45deg);

  @include mq(md) {
    width: rem(14);
    height: rem(14);
  }
}
