@use "global" as *;


.sub-menu {
  display: none;  // 

  min-width: rem(200);
  background: $color-white;
  border-top: 4px solid $color-accent2;
  border-left: 1px solid #b7c7e2;
  border-right: 1px solid #b7c7e2;
  border-bottom: 1px solid #b7c7e2;

  position: absolute; /*  */
  top: 100%; /*  */
  left: 50%;
  transform: translateX(-50%);
  z-index: 800;  // fv
}

.header__nav-item:hover .sub-menu {  // hover
  display: block;

  @include mq(md) {
    display: none;
  }
}

.sub-menu__items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sub-menu__item {
  display: block;
  width: 100%;
  text-align: left;
  padding-block: rem(16);
  // padding-inline: rem(12);
  padding-left: rem(18);
  padding-right: rem(8);
  color: $color-font-base-1;
  font-size: rem(15);
  font-weight: $normal;
  transition: all 0.2s ease-out;
  position: relative;
}

.sub-menu__item::after {  // 
  content: "";
  width: 86%;
  height: 1px;

  background: repeating-linear-gradient(  /*  */
    to right,         /*  */
    black,            /*  */
    black 1px,        /*  */
    transparent 1.5px,  /*  */
    transparent 5px   /*  */
  );
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.2s ease-out;
}

.sub-menu__item:last-of-type::after {  // 
  display: none;
}

.sub-menu__item:hover {  // hover
  background: #e6eef7;
  color: $color-accent3;
}
