@use "global" as *;


.post {
  padding-block: rem(20);
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: rem(20);

  @include mq(md) {
    padding-block: rem(18);
    flex-direction: column;
    justify-content: center;
    gap: rem(15);
  }
}

.post:nth-of-type(n + 2) {
  position: relative;
}

.post:nth-of-type(n + 2)::before {  // 
  content: "";
  width: 100%;
  height: 1px;

  background: repeating-linear-gradient(  /*  */
    to right,         /*  */
    black,            /*  */
    black 1px,        /*  */
    transparent 1.5px,  /*  */
    transparent 5px   /*  */
  );
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.2s ease-out;
}

.post__meta {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(18);

  @include mq(md) {
    gap: rem(7);
  }
}

.post__category {
  text-align: center;
  font-size: rem(13);
  font-weight: $semiBold;
  color: $color-white;
  padding-block: rem(5);
  padding-inline: rem(5);  // 
  min-width: rem(100);
  border-radius: rem(15);

  background: #60606a;  // 

  @include mq(md) {
    font-size: rem(12);
    padding-block: rem(4);
    padding-inline: rem(4);  // 
  }
}

.post__category.is-notice {  // 
  background: #f4aa0a;
}

.post__category.is-topics {  // 
  background: #67a5e3;
}

.post__category.is-other {  // 
  background: #9eacb9;
}

.post__date {
  font-size: rem(16);
  font-weight: $normal;
  color: $color-font-base-2;

  @include mq(md) {
    font-size: rem(15);
  }
}

.post__link-text {
  text-decoration: underline;  // 
  text-decoration-color: rgba(0, 0, 0, 0.5); /* RGBA */
  width: 100%;
  font-size: rem(16);
  font-weight: $normal;
  color: #202020;
  line-height: 2.0;
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    font-size: rem(14);
  }
}

.post__link-text:hover {  // hover
  opacity: 0.6;
}




// =======================================
// 
// =======================================
// post--main (NEWSpost (TOP))
.post--main {

}

.post--main .post__link-text {
  font-size: rem(17);

  @include mq(md) {
    font-size: rem(14);
  }
}
