.form__fields {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}




//  (Form)
.form__confirmbtn-wrap {
  margin-top: rem(40);
  width: fit-content;
  margin-inline: auto;

  @include mq(md) {
    margin-top: rem(30);
  }
}

.form__confirmbtn {
  min-width: rem(315);

  @include mq(md) {
    min-width: rem(250);
  }
}



//  ()
.form__confirm-text {
  margin-top: rem(25);
  font-size: rem(16);
  font-weight: $normal;
  line-height: 1.875;
  color: #40444a;

  @include mq(md) {
    margin-top: rem(15);
    font-size: rem(13);
    line-height: 1.6;
  }
}

.form__confirm-text-alert {  // 
  font-size: rem(16);
  font-weight: $normal;
  line-height: 1.875;
  color: #e04c5f;

  @include mq(md) {
    font-size: rem(13);
    line-height: 1.6;
  }
}

.form__confirm-buttons {
  margin-top: rem(40);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(40);

  @include mq(md) {
    margin-top: rem(20);
    flex-direction: column;
    align-items: center;
    gap: rem(15);
  }
}

//  ()
.form__returnbtn {
  min-width: rem(315);
  width: fit-content;

  @include mq(md) {
    min-width: rem(250);
  }
}

//  ()
.form__sendbtn-wrap {  // <input>wrap
  position: relative;
}

.form__sendbtn-wrap:hover::after {  // hover
  transform: translate(rem(5), -50%);
}

.form__sendbtn-wrap::after {  // 
  display: inline-block;
  content: "";
  width: rem(85);
  height: rem(10.86);
  background: url(../images/common/btn2-arrow-white.webp) no-repeat center center / contain;
  position: absolute;
  top: 50%;
  right: rem(60);
  transform: translateY(-50%);
  transition: transform 0.2s ease-out;

  @include mq(md) {
    right: rem(35);
  }
}

.form__sendbtn {
  text-align: left;
  padding-left: rem(70);  // 
  min-width: rem(315);
  width: fit-content;

  display: inline-flex;
  border-radius: rem(20) 0 rem(20) 0;
  padding-top: rem(15);
  padding-bottom: rem(15);
  // padding-inline: rem(15);  // 
  background: #3e4c61;
  justify-content: center;
  align-items: center;
  gap: rem(25);
  transition: background 0.2s ease-out;

  font-size: rem(17);
  font-weight: $medium;
  color: $color-white;

  @include mq(md) {
    min-width: rem(250);
    padding-left: rem(35);
  }
}









// ======================================================================
// Contact Form 7 ()
// ======================================================================
.wpcf7-spinner {
  display: none !important;
}
